import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { WarrantyInvoiceComponent } from './warranty-invoice.component';
import { EditWarrantyInvoiceComponent } from './edit-warranty-invoice/edit-warranty-invoice.component';
import { DetailComponent } from './detail/detail.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { FormsModule } from '@angular/forms'; 
import { TranslateModule } from '@ngx-translate/core'; 
import { BillType15Module } from 'src/app/sales/components/bill-type/bill-type-15/bill-type-15.module';
const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: "",
        component: WarrantyInvoiceComponent
      },
      {
        path: "detail",
        component: DetailComponent
      },
      {
        path: "edit",
        component: EditWarrantyInvoiceComponent,
        
      },
    ]
  },

]

@NgModule({
  declarations: [WarrantyInvoiceComponent, EditWarrantyInvoiceComponent, DetailComponent],
  imports: [
    CommonModule, 
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    BillType15Module
  ]
})
export class WarrantyInvoiceModule { }
